.projects-section {
	background: transparent;
	height: 80vh;
}

.projects-section h1 {
	color: white;
	font-size: 36px;
	opacity: 0.9;
	font-weight: bold;
}

.projects-container {
	margin-top: 2vh;
	width: 62vw;
	height: 62vh;

	display: grid;
	grid-gap: 1rem;
	background: rgba(0, 0, 0, 0.068);
}

#projects-list {
	width: 15%;
	height: 62vh;
	background: transparent;
	display: flex;
	flex-direction: column;
}

.projects-list-card {
	width: 100%;
	height: 3vh;
	font-size: 2.2vh;
	vertical-align: center;
	text-align: center;
	color: white;
	/*border: white solid;*/
	opacity: 0.5;
	margin-bottom: 8px;
	display: inline-block;
	transition: all 0.3s;
}

.projects-list-card:hover {
	opacity: 0.7;
}

.projects-list-card:after {
	background: none repeat scroll 0 0 transparent;
	bottom: 0;
	content: "";
	display: block;
	height: 2px;
	left: 50%;
	position: absolute;
	background: #fff;
	transition: width 0.3s ease 0s, left 0.3s ease 0s;
	width: 0;
}

.projects-list-card:hover:after {
	width: 100%; 
  	left: 0; 
}