.home-section {
	background: transparent;
	height: 80vh;
}

.home-section h1 {
	font-weight: 100;
	font-size: 4.5vw;
	color: white;
	opacity: 0.9;
}

.home-section h2 {
	font-weight: 100;
	font-size: 18px;
	color: lightgray;
	opacity: 0.5;
}

.home-section button {
	margin-top: 28px;
	font-weight: normal;
	font-size: 32px;
	color: black;
	mix-blend-mode: screen;
	opacity: 0.6;
	background-color: white;
	padding: 4px 16px;
	border-radius: 32px;
	transition: opacity 0.1s;
}

.home-section button:hover {
	opacity: 1;
}